
import axios from '@/network'

import Qs from 'qs'

// 
export const userlist = () => {
  return axios.request({
    url: '/api/user/list',
    method: 'GET'
  })
}
// 
export const login = (obj) => {
  return axios.request({
    url: '/api/user/login',
    data: Qs.stringify(obj),
    method: 'POST'
  })
}
export const regist = (obj) => {
  return axios.request({
    url: '/api/user/regist',
    data: Qs.stringify(obj),
    method: 'POST'
  })
}